<template>
  <div>
    <div class="box">
      <h1 id="const" class="title is-2">Waiting For Element Locator to be Selected in Selection</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div
          class="message-body"
        >Selenium can wait for the element to be selected using locators. This is useful for waith a checkboxes and select elements 
        button to be selected.
        </div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Browser goes to https://seleniumbyexamples.github.io/scrapingapp/seleniumwait</li>
              <li>Call the Method 6 times which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">HTML Code</h1>
      This is the HTML code for the checkboxes and select elements.
      <prism language="scss">{{ htmlCode }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>

      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitElLocatorSelection",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.common.by import By
from selenium.webdriver.support.ui import WebDriverWait
from selenium.webdriver.support import expected_conditions as EC
from selenium.common.exceptions import TimeoutException
from datetime import datetime

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://seleniumbyexamples.github.io/scrapingapp/seleniumwait")

def wait_for_element_selection(wait_time: int, selector: str, is_selected: bool):
    try:
        print("[{}] Waiting element {}".format(str(datetime.now()), selector))
        WebDriverWait(driver, wait_time).until(
            EC.element_located_selection_state_to_be((By.CSS_SELECTOR, selector), is_selected)
        )
        print("[{}] Element found".format(str(datetime.now())))
    except TimeoutException as e:
        print("[{}] Element not found".format(str(datetime.now())))

wait_for_element_selection(3, "div#elementSelection input[name='forSelected']", True)
wait_for_element_selection(6, "div#elementSelection input[name='forSelected']", False)
wait_for_element_selection(9, "div#elementSelection input[name='notSelected']", True)
wait_for_element_selection(12, "div#elementSelection input[name='notSelected']", False)
wait_for_element_selection(15, "div#elementSelection option[value='apple']", True)
wait_for_element_selection(18, "div#elementSelection option[value='apple']", False)
driver.close()
      `,
      output: `[2020-06-15 20:04:10.479380] Waiting element div#elementSelection input[name='forSelected']
[2020-06-15 20:04:10.943379] Element found
[2020-06-15 20:04:10.943379] Waiting element div#elementSelection input[name='forSelected']
[2020-06-15 20:04:17.467676] Element not found
[2020-06-15 20:04:17.467676] Waiting element div#elementSelection input[name='notSelected']
[2020-06-15 20:04:26.540074] Element not found
[2020-06-15 20:04:26.540074] Waiting element div#elementSelection input[name='notSelected']
[2020-06-15 20:04:26.555689] Element found
[2020-06-15 20:04:26.555689] Waiting element div#elementSelection option[value='apple']
[2020-06-15 20:04:26.586957] Element found
[2020-06-15 20:04:26.586957] Waiting element div#elementSelection option[value='apple']
[2020-06-15 20:04:44.722361] Element not found`,
      htmlCode: `
<div id="elementSelection">
  <div>
    <input type="checkbox" name="forSelected" value="Selected" checked="checked"> Selected Checkbox <br> 
    <input type="checkbox" name="notSelected" value="Not Selected"> Not Selected Checkbox <br> 
    <select name="fruitSelection">
      <option value="orange">Orange</option> 
      <option value="apple" selected="selected">Apple</option> 
      <option value="pomelo">Pomelo</option>
    </select>
  </div>
</div>
      `
    };
  }
};
</script>
