<template>
  <div>
    <div class="box">
      <h1 id="const" class="title is-2">Waiting For Presence of An Element in a Page</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">Selenium can wait for <b>an</b> element to be present. The element can either be visible or invisble.
        </div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h2 id="const" class="title is-3">What The Program Does?</h2>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Browser goes to https://seleniumbyexamples.github.io/scrapingapp/</li>
              <li>Call the Method 3 times which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>
      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitPresenceOne",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.common.by import By
from selenium.webdriver.support.ui import WebDriverWait
from selenium.webdriver.support import expected_conditions as EC
from selenium.common.exceptions import TimeoutException
from datetime import datetime

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://seleniumbyexamples.github.io/scrapingapp/")

def wait_for_the_element(wait_time: int, el_id: str):
    try:
        print("[{}] Finding element {}".format(str(datetime.now()), el_id))
        WebDriverWait(driver, wait_time).until(
            EC.presence_of_all_elements_located((By.ID, el_id))
        )
        print("[{}] Element found".format(str(datetime.now())))
    except TimeoutException as e:
        print("[{}] Element did not load".format(str(datetime.now())))

wait_for_the_element(3, "navMenuId")
wait_for_the_element(6, "noneExistentId")
wait_for_the_element(9, "anotherNoneExistentId")
driver.close()
      `,
      output: `[2020-06-16 16:08:23.390080] Finding element navMenuId
[2020-06-16 16:08:23.421338] Element found
[2020-06-16 16:08:23.421338] Finding element noneExistentId
[2020-06-16 16:08:29.502464] Element did not load
[2020-06-16 16:08:29.502464] Finding element anotherNoneExistentId
[2020-06-16 16:08:38.653380] Element did not load
`
    };
  }
};
</script>
