<script>
export default {
  name: "LocatorCSSSelector",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.common.by import By
import time

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://seleniumbyexamples.github.io/scrapingapp/seleniumlocator")

time.sleep(2)
el = driver.find_element(By.CSS_SELECTOR, "div#locatorCSSSelector")
print(el.text)
driver.quit()
      `,
      htmlCode: `
<div id="locatorCSSSelector">This is for locator CSS SELECTOR</div>
      `
    };
  }
