    <div class="box">
      <h2 id="const" class="title is-3">What The Program Does?</h2>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
            <li>Open the firefox browser</li>
            <li>Browser goes to https://seleniumbyexamples.github.io/scrapingapp/seleniumlocators</li>
            <li>Wait for 2 seconds</li>
            <li>Finds elements using CSS class name and Lcoator</li>
