<script>
export default {
  name: "FindElsByLocator",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.common.by import By
import time

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://seleniumbyexamples.github.io/scrapingapp/seleniumlocators")

time.sleep(2)
elements = driver.find_elements(By.CLASS_NAME, "is-medium")
for el in elements:
    print(el.text)
driver.quit()
      `
    };
  }
};
</script>
