<template>
  <div>
    <div class="box">
      <h1 id="const" class="title is-2">Script Execution</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">By executing a script you can also navigate through pages using selenium. </div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h2 id="const" class="title is-3">What The Program Does?</h2>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
            <li>Open the firefox browser</li>
            <li>Execute a script to navigate to https://seleniumbyexamples.github.io/scrapingapp/about</li>
            <li>Halts for 10 seconds</li>
            <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
  </div>
</template>

<script>
export default {
  name: "NavScript",
  data() {
    return {
      code: `
from selenium import webdriver
import time

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.execute_script("window.location.href='https://seleniumbyexamples.github.io/scrapingapp/about';")
time.sleep(10)

driver.close()
      `
    };
  }
};
</script>
