<template>
  <div>
    <div class="box">
      <h1 id="const" class="title is-2">Anchor Element Click</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">When you click an anchor tag in a web page you can navigate, to other pages. 
          This can also be done by selenium driver.
        </div>
      </article>
    </div>
    <div class="box">
      <h2 id="const" class="title is-3">Example Code</h2>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h2 id="const" class="title is-3">What the program does?</h2>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
            <li>Open the firefox browser</li>
            <li>Browser goes to https://seleniumbyexamples.github.io/scrapingapp/</li>
            <li>Halts for 5 seconds</li>
            <li>
              Finds the anchor tag for the
              <b>About</b> in the top menu
            </li>
            <li>Clicks the link</li>
            <li>Halts for 10 seconds</li>
            <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
  </div>
</template>

<script>
export default {
  name: "NavAnchor",
  data() {
    return {
      code: `
from selenium import webdriver
import time

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://seleniumbyexamples.github.io/scrapingapp/")
time.sleep(5)

el = driver.find_element_by_css_selector("div[class='navbar-start'] > a:last-of-type")
el.click()
time.sleep(10)

driver.close()
      `
    };
  }
};
</script>
