<template>
  <div>
    <div class="box">
      <h1 id="const" class="title is-2">Browsing History</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">The forward or backward (browser history) can be used in selenium.</div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h2 id="const" class="title is-3">What The Program Does?</h2>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
            <li>Open the firefox browser</li>
            <li>Browser goes to https://seleniumbyexamples.github.io/scrapingapp/</li>
            <li>Halts for 3 seconds</li>
            <li>Browser goes to https://seleniumbyexamples.github.io/scrapingapp/about</li>
            <li>Halts for 3 seconds</li>
            <li>Browser goes to https://seleniumbyexamples.github.io/scrapingapp/ using the back of history</li>
            <li>Halts for 10 seconds</li>
            <li>Browser goes to https://seleniumbyexamples.github.io/scrapingapp/about using the forward of history</li>
            <li>Halts for 10 seconds</li>
            <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
  </div>
</template>

<script>
export default {
  name: "NavHistory",
  data() {
    return {
      code: `
from selenium import webdriver
import time

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://seleniumbyexamples.github.io/scrapingapp/")
time.sleep(3)
driver.get("https://seleniumbyexamples.github.io/scrapingapp/about")
time.sleep(3)

driver.back()
time.sleep(10)

driver.forward()
time.sleep(10)

driver.close()
      `
    };
  }
};
</script>
