<template>
  <div>
    <div class="box">
      <h1 id="const" class="title is-2">Waiting For Visibility of An Element Using Locator</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">Selenium can wait for <b>an</b> elements to be visible given a locator.
        </div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h2 id="const" class="title is-3">What The Program Does?</h2>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Browser goes to https://seleniumbyexamples.github.io/scrapingapp/seleniumwait</li>
              <li>Call the Method 4 times which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">HTML Code</h1>
      This is the HTML code for the elements visibility.
      <prism language="scss">{{ htmlCode }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>
      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitVisibilityOne",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.common.by import By
from selenium.webdriver.support.ui import WebDriverWait
from selenium.webdriver.support import expected_conditions as EC
from selenium.common.exceptions import TimeoutException
from datetime import datetime

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://seleniumbyexamples.github.io/scrapingapp/seleniumwait")

def wait_for_the_element(wait_time: int, selector: str):
    try:
        print("[{}] Finding element {}".format(str(datetime.now()), selector))
        WebDriverWait(driver, wait_time).until(
            EC.visibility_of_element_located((By.CSS_SELECTOR, selector))
        )
        print("[{}] Element found".format(str(datetime.now())))
    except TimeoutException as e:
        print("[{}] Element did not load".format(str(datetime.now())))

wait_for_the_element(3, "div#hiddenElements > div:nth-of-type(3)")
wait_for_the_element(6, "div#hiddenElements > div:nth-of-type(1)")
wait_for_the_element(9, "div#hiddenElements > div:nth-of-type(2)")
wait_for_the_element(12, "div#hiddenElements > div")
driver.close()
      `,
      output: `[2020-08-23 22:02:10.044050] Finding element div#hiddenElements > div:nth-of-type(3)
[2020-08-23 22:02:12.366050] Element found
[2020-08-23 22:02:12.366050] Finding element div#hiddenElements > div:nth-of-type(1)
[2020-08-23 22:02:18.848767] Element did not load
[2020-08-23 22:02:18.848767] Finding element div#hiddenElements > div:nth-of-type(2)
[2020-08-23 22:02:28.141907] Element did not load
[2020-08-23 22:02:28.141907] Finding element div#hiddenElements > div
[2020-08-23 22:02:40.689179] Element did not load
`,
      htmlCode: `
<div id="hiddenElements">
  <div style="display: none;">This is a hidden element</div> 
  <div style="width: 0px; height: 0px;"></div> 
  <div>Visible div</div>
</div>
      `
    };
  }
};
</script>
