<template>
  <div>
    <div class="box">
      <h1 id="const" class="title is-2">Waiting For Elements Text</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">Selenium can wait for the innrHTML text value of an element.
        </div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h2 id="const" class="title is-3">What The Program Does?</h2>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Browser goes to https://seleniumbyexamples.github.io/scrapingapp/seleniumwait</li>
              <li>Call the Method 3 times which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">HTML Code</h1>
      This is the HTML code for the elements text.
      <prism language="scss">{{ htmlCode }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>
      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitText",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.common.by import By
from selenium.webdriver.support.ui import WebDriverWait
from selenium.webdriver.support import expected_conditions as EC
from selenium.common.exceptions import TimeoutException
from datetime import datetime

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://seleniumbyexamples.github.io/scrapingapp/seleniumwait")

def wait_for_text(wait_time: int, selector: str, text_value: str):
    try:
        print("[{}] Finding text {}".format(str(datetime.now()), selector))
        WebDriverWait(driver, wait_time).until(
            EC.text_to_be_present_in_element((By.CSS_SELECTOR, selector), text_value)
        )
        print("[{}] Text found".format(str(datetime.now())))
    except TimeoutException as e:
        print("[{}] Text did not load".format(str(datetime.now())))

wait_for_text(3, "div#textExpectation > div:nth-of-type(1)", "Expected Text")
wait_for_text(6, "div#textExpectation > div:nth-of-type(2)", "Expected Text")
wait_for_text(9, "div#textExpectation > div:nth-of-type(1)", "expected text")
driver.close()
      `,
      output: `[2020-06-17 13:15:06.472584] Finding text div#textExpectation > div:nth-of-type(1)
[2020-06-17 13:15:06.588517] Text found
[2020-06-17 13:15:06.588517] Finding text div#textExpectation > div:nth-of-type(2)
[2020-06-17 13:15:13.024974] Text did not load
[2020-06-17 13:15:13.024974] Finding text div#textExpectation > div:nth-of-type(1)
[2020-06-17 13:15:22.097977] Text did not load
`,
      htmlCode: `
<div id="textExpectation">
  <div>Expected Text</div> 
  <div>
    <input type="text" value="This is an input">
  </div>
</div>
      `
    };
  }
};
</script>
