<template>
  <div>
    <div class="box">
      <h1 id="const" class="title is-2">Manipulating Form - Button</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">Selenium manipulate buttons in different ways. The example below is how to control a button in a page.</div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h2 id="const" class="title is-3">What The Program Does?</h2>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
            <li>Open the firefox browser</li>
            <li>Browser goes to https://seleniumbyexamples.github.io/scrapingapp/forminput</li>
            <li>Wait for 5 seconds</li>
            <li>Finds the first button under the div with id formButtons (enabled button).</li>
            <li>Click the found button</li>
            <li>Wait for 5 seconds</li>
            <li>Dismiss the alert popup</li>
            <li>Disable the button</li>
            <li>Wait for 5 seconds</li>
            <li>Click the same button (now disabled.). Nothing happens</li>
            <li>Wait for 5 seconds</li>
            <li>Finds the second button under the div with id formButtons (disabled button).</li>
            <li>Click the second button (disabled.). Nothing happens</li>
            <li>Wait for 5 seconds</li>
            <li>Enable the second button</li>
            <li>Change the value to I am not disabled</li>
            <li>Wait for 5 seconds</li>
            <li>Click the found button</li>
            <li>Wait for 5 seconds</li>
            <li>Dismiss the alert popup</li>
            <li>Wait for 5 seconds</li>
            <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">HTML Code</h1>
      <prism language="scss">{{ htmlCode }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "FormButton",
  data() {
    return {
      code: `
from selenium import webdriver
import time

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://seleniumbyexamples.github.io/scrapingapp/forminput")

time.sleep(5)
el = driver.find_element_by_css_selector("#formButtons input:nth-of-type(1)")
el.click()

time.sleep(5)
alert = driver.switch_to.alert
alert.dismiss()
driver.execute_script("arguments[0].disabled='disabled'", el)

time.sleep(5)
el.click()

el = driver.find_element_by_css_selector("#formButtons input:nth-of-type(2)")
el.click()

time.sleep(5)
driver.execute_script("arguments[0].disabled=''", el)
driver.execute_script("arguments[0].value='I am not disabled'", el)

time.sleep(5)
el.click()

time.sleep(5)
alert = driver.switch_to.alert
alert.dismiss()

time.sleep(5)
driver.quit()
      `,
      htmlCode: `
<div id="formButtons" class="column is-one-quarter divider-top">
  <div class="field">
    <label class="label">Buttons</label>
    <div class="control">
      <input type="button" value="Click Me" class="button">
      <input type="button" value="Im Disabled" disabled="disabled" class="button">
    </div>
  </div>
</div>
      `
    };
  }
};
</script>
