<template>
  <div>
    <div class="box">
      <h1 id="const" class="title is-2">Waiting For Element to be Selected in Selection</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div
          class="message-body"
        >Selenium can wait for the element to be selected. This is useful for waith a checkboxes and select elements 
        button to be selected.
        </div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h2 id="const" class="title is-3">What The Program Does?</h2>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Browser goes to https://seleniumbyexamples.github.io/scrapingapp/seleniumwait</li>
              <li>Finds a selected check box and puts it in a variable</li>
              <li>Finds an unchecked check box and puts it in a variable.</li>
              <li>Finds an option of select and puts it in a variable.</li>
              <li>Call the Method 6 times which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">HTML Code</h1>
      This is the HTML code for the checkboxes and select elements.
      <prism language="scss">{{ htmlCode }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>

      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitElSelection",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.support.ui import WebDriverWait
from selenium.webdriver.support import expected_conditions as EC
from selenium.common.exceptions import TimeoutException
from datetime import datetime
import time

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://seleniumbyexamples.github.io/scrapingapp/seleniumwait")

def wait_for_element_selection(wait_time: int, element, is_selected: bool):
    try:
        print("[{}] Waiting element".format(str(datetime.now())))
        WebDriverWait(driver, wait_time).until(
            EC.element_selection_state_to_be(element, is_selected)
        )
        print("[{}] Element found".format(str(datetime.now())))
    except TimeoutException as e:
        print("[{}] Element not found".format(str(datetime.now())))

time.sleep(2)
check_el = driver.find_element_by_css_selector("div#elementSelection input[name='forSelected']")
uncheck_el = driver.find_element_by_css_selector("div#elementSelection input[name='notSelected']")
option_el = driver.find_element_by_css_selector("div#elementSelection option[value='apple']")

wait_for_element_selection(3, check_el, True)
wait_for_element_selection(6, check_el, False)
wait_for_element_selection(9, uncheck_el, True)
wait_for_element_selection(12, uncheck_el, False)
wait_for_element_selection(15, option_el, True)
wait_for_element_selection(18, option_el, False)
driver.close()
      `,
      output: `[2020-06-15 19:25:11.478402] Waiting element
[2020-06-15 19:25:11.531841] Element found
[2020-06-15 19:25:11.531841] Waiting element
[2020-06-15 19:25:17.676089] Element not found
[2020-06-15 19:25:17.676089] Waiting element
[2020-06-15 19:25:26.808282] Element not found
[2020-06-15 19:25:26.808282] Waiting element
[2020-06-15 19:25:26.810342] Element found
[2020-06-15 19:25:26.810342] Waiting element
[2020-06-15 19:25:26.820508] Element found
[2020-06-15 19:25:26.820508] Waiting element
[2020-06-15 19:25:44.906554] Element not found`,
      htmlCode: `
<div id="elementSelection">
  <div>
    <input type="checkbox" name="forSelected" value="Selected" checked="checked"> Selected Checkbox <br> 
    <input type="checkbox" name="notSelected" value="Not Selected"> Not Selected Checkbox <br> 
    <select name="fruitSelection">
      <option value="orange">Orange</option> 
      <option value="apple" selected="selected">Apple</option> 
      <option value="pomelo">Pomelo</option>
    </select>
  </div>
</div>
      `
    };
  }
};
</script>
